<script>
    import {mapActions, mapState} from 'vuex'
    import Footer from "./Footer";
    import HeaderTicket from "./HeaderTicket";
    import Modal from "./Modal";

    export default {
        name: "Tickets",
        data() {
            return {
                loading: false,
                client: {},

                //button
                showActive: true,
                //
                loginType: '',
                isActive: true,
                isArhive: false,

                //tickets
                ticketsActive: [],

                ticketsArchive: [],

                isOpenModal: false,
            }
        },
        computed: {
            ...mapState([
                'token', 'isAuth'
            ]),
            ticketsLink() {
                return this.$route.name === 'tickets' ? '/ticketsbus' : '/'
            }
        },
        created() {
            this.loading = true
            const headers = {
                'Content-type': 'application/json',
                'Authorization': `Bearer ${this.token}`, //передача токена
            }
            //get USER information
            axios.get('web/user', {
                headers: headers
            })
                .then((response) => {
                    this.client = response.data.result
                })


            //    get USER TICKETS
            axios.get('web/ticket/my', {
                headers: headers
            })
                .then((response) => {
                    console.log(response);
                    this.ticketsActive = response.data.result.active
                    this.ticketsArchive = response.data.result.archive


                })
            this.loading = false
        },
        components: {
            Footer,
            HeaderTicket,
            Modal
        },
        methods: {
            openModal() {
                this.isOpenModal = true;
            },
            closeModal() {
                this.isOpenModal = false;
            },
        }
    }
</script>

